import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import { LitElement, html, css, customElement, property } from "@umbraco-cms/backoffice/external/lit";

@customElement('umbracotours-dashboard')
export class UmbracoToursDashboard extends UmbElementMixin(LitElement) {

    constructor() {
        super();
    }

    @property()
    title = 'UmbracoTours dashboard'

    render() {
        return html`
            <uui-box headline="${this.title}">
                dashboard content goes here
            </uui-box>
        `
    }

    static styles = css`
        :host {
            display: block;
            padding: 20px;
        }
    `
}


export default UmbracoToursDashboard;

declare global {
    interface HtmlElementTagNameMap {
        'UmbracoTours-dashboard': UmbracoToursDashboard
    }
}